<template>
<div class="page-content">
    <h1 class="h1">Toast </h1>
    <div class="doc-text">Toast </div>
    <h2 class="h2"></h2>
<div class="code-pre" v-highlight>
<pre>
<code>import { createApp } from 'vue';
import { Toast } from 'lity-ui';
const app = createApp();
app.use(Toast);</code>
</pre>
</div>
<h2 class="h2"></h2>
<div class="doc-text"></div>
<div class="code-pre" v-highlight>
<pre>
<code>setup (props) {
    function ToastClick () {
        Toast({ msg: ''})
    }
    return {
        ToastClick
    }
}</code>
</pre>
</div>
<lity-button :success="true" @click="ToastClick"></lity-button>

<h2 class="h2"></h2>
<div class="doc-text"> <code class="code-tag">img_url</code>,png</div>
<div class="code-pre" v-highlight>
<pre>
<code>setup (props) {
    function ToastClick () {
        Toast({ msg: '', img_url: 'https://b.yzcdn.cn/vant/logo/github.svg', time: 3000 })
    }
    return {
        ToastClick
    }
}</code>
</pre>
</div>
<lity-button :success="true" @click="ToastClick2"></lity-button>

<h2 class="h2">Toast </h2>
<table class="table">
<thead><tr><th></th> <th></th><th></th><th></th></tr></thead>
<tbody>
  <tr>
    <td>msg</td> <td></td><td>String</td> <td>-</td>
  </tr>
  <tr>
    <td>imgurl</td> <td></td><td>String</td> <td>-</td>
  </tr>
   <tr>
    <td>time</td> <td></td><td>Number</td> <td>3000</td>
  </tr>
</tbody>
</table>
</div>
</template>

<script>
import LityButton from '../components/button/button'
import Toast from '../components/toast/index'
export default {
  components: {
    LityButton
  },
  setup (props) {
    function ToastClick () {
      Toast({ msg: '' })
    }
    function ToastClick2 () {
      Toast({ msg: '', imgurl: 'https://b.yzcdn.cn/vant/logo/github.svg', time: 3000 })
    }
    return {
      ToastClick,
      ToastClick2
    }
  }
}
</script>

